import React, {Component} from 'react';
import style from '../public/css/side.scss';

class Side extends Component{
    constructor (props) {
        super(props)
    }
    render () {
        return (
            <div className={style.screen}>
                <div className={style.side}>
                    <User />
                    <NavList />
                    <Signout />
                </div>
            </div>
        )
    }
}

class User extends Component{
    render () {
        return (
            <div className={style.user}>
                <div className={style.avatar}>
                    <img  src={require('../public/img/avatar.jpg')} />
                </div>
                <div className={style.userMsg}>
                    <div className={style.nikename}>
                        <span>183****8205</span>
                        <span></span>
                    </div>
                    <div className={style.bio}>
                        <span>: </span>
                        
                    </div>
                </div>
            </div>
        )
    }
}

class NavList extends  Component{
    render () {
        return (
            <ul>
                <li className={style.active}>
                    <img src={require('../public/img/home_icon.png')}/>
                    <span></span>
                </li>
                <li>
                    <img src={require('../public/img/my_icon.png')}/>
                    <span></span>
                </li>
                <li>
                    <img src={require('../public/img/post_icon.png')}/>
                    <span></span>
                </li>
                <li>
                    <img src={require('../public/img/message_icon.png')}/>
                    <span></span>
                </li>
            </ul>
        )
    }
}

class Signout extends Component{
    render () {
        return (
            <div className={style.signout}>
                <button></button>
            </div>
        )
    }
}


export default Side